import React from "react";
import WeddingInvite from "./components/WeddingInvite"; // Import the WeddingInvite component
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";

const App = () => (
  <Router>
    <Routes>
      <Route path="/wedding/:username" element={<WeddingInvite />} />
      <Route path="/wedding" element={<WeddingInvite />} />
    </Routes>
  </Router>
);

export default App;